import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class FoodService {

  getAllCatUrl = 'http://localhost:5000/foods/getAllCat'
  getFoodsByCatIdUrl = 'http://localhost:5000/foods/getFoodsByCatId/'

  constructor(private http: HttpClient) { }
  getAllCat(){
    return <any> this.http.get(this.getAllCatUrl)
  }

  getFoodsByCatId(catId){
    return <any> this.http.get(this.getFoodsByCatIdUrl+catId);
  }

  getImage(id){
    return <any>this.http.get('http://localhost:5000/files/getImage/'+id);
  }

  getFoodById(fid){
    return <any> this.http.get('http://localhost:5000/foods/getFoodById/'+fid)
  }

  placeOrder(order){
    return <any> this.http.post('http://localhost:5000/order/placeOrder',order);
  }
}
