import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  signUpUrl = 'http://localhost:5000/userAuth/signUp';
  loginUrl = 'http://localhost:5000/userAuth/logIn';
  constructor(private http: HttpClient) { }

  signUp(form){
    console.log(form);
    return <any> this.http.post(this.signUpUrl,form);
  }

  logIn(form){
    return <any> this.http.post(this.loginUrl,form);
  }

  getUser(){
    return <any> this.http.get('http://localhost:5000/userAuth/getUser');
  }

  getToken(){
    return localStorage.getItem('token');
  }

  getOrdersByUserId(id){
    return <any>this.http.get('http://localhost:5000/order/getOrdersByUserId/'+id)
  }
}

